.App {
  background-color: #282c34;
  text-align: left;
  width: auto;
  min-height: 100vh;
}

button {
  margin: auto;
  padding: .25rem .75rem;
  font-size: 1.4rem;
  cursor: pointer;
  background: rgb(175, 185, 34);
  color: white;
  border: 2px solid rgb(175, 185, 34);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

h1 {
  margin: 1.2rem auto;
  font-family: sans-serif;
  font-size: 3.5rem;
  text-align: center;
}

blockquote {
  border-top: 2px solid rgb(175, 185, 34);
  border-bottom: 2px solid rgb(175, 185, 34);
  font-size: 2rem;
  margin: 5% 8%;
  font-weight: 200;
  padding: 2%;
}

small {
  display: block;
  font-size: 0.7em;
  text-align: right;
  font-style: italic;
  margin: 10px;
  padding: 4px;
}

.App-header, .App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}